/* HomePage.css */
.hero-section {
    height: 500px; /* Set the height you want for your image container */
    background-image: url(/Ecom-Site/static/media/GatorWrapsTruck.69e643d7.png); 
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
  }
  .break {
    border-top: 2px solid #586856;
    margin: 20px auto; /* 'auto' horizontally centers the div if a width is set */
    width: 80%; /* Adjust the width as necessary, or use a max-width */
    max-width: 1200px; /* This limits how wide the line can get */
  }
  .product {
    display: flex;
    align-items: flex-start; /* Aligns items to the top */
    justify-content: space-between;
    padding: 20px 0;
    max-width: 1200px; /* Or whatever maximum width you prefer */
    margin: auto; /* This will center the .product container */
  }
  .product-text .lead-text{
    text-align: center;
    font-family: 'Merriweather', serif;
    font-size: 26px;
    color: #90c989;
  }
  
  .product-text {
    flex: 1 1; /* Allows this item to grow and shrink as needed */
    padding-right: 20px; /* Adds some space between the text and the images */
  }
  .product-text p {
    margin-bottom: 1em; /* Adds more vertical space after paragraphs */
    line-height: 1.8; /* Increases the line spacing */
    font-family: 'Merriweather', serif;
  }
  
  .product-images {
    display: flex;
    flex: 2 1; /* Allows this item to take up twice as much space as .product-text */
    justify-content: space-between; /* Distributes space between the images */
  }
  
  .product-images img {
    width: 100%; /* Each image will try to fill 100% of its container */
    max-width: 200px; /* Decrease max-width to make images smaller */
    height: auto; /* Maintain the aspect ratio */
    margin: 0 10px; /* Add some horizontal space between images */
  }
  .product-item {
    text-align: center; /* Centers the content */
    margin: 0 10px; /* Maintains horizontal space between items */
    font-size: 20px;
  }
  .product-item img {
    width: 100%; /* Each image will try to fill 100% of its container */
    max-width: 200px; /* Sets the maximum width for the image */
    height: auto; /* Maintain the aspect ratio */
    display: block; /* Ensures the image is a block-level element, allowing text to appear below it */
    margin-bottom: 8px; /* Adds space between the image and the text below */
  }
  
  
  @media (max-width: 768px) {
    .hero-section {
      height: 300px; /* Smaller height for mobile devices */
    }
  }
  
.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 1rem;
}
/* 586856*/

.product-card {
  width: 300px; /* Suitable width for each card */
  border: 1px solid #ccc; /* Subtle border */
  border-radius: 8px; /* Rounded corners */
  overflow: hidden; /* Ensures content fits neatly in the card */
  margin: 1rem; /* Spacing around each card */
  text-align: center; /* Center-aligns the content */
  background: #c8c6c6; /* White background for the card */
  font-family: 'Merriweather', serif; /* Merriweather font for consistency */
}

.product-card img {
  width: 100%; /* Image takes the full width of the card */
  height: auto; /* Maintains the aspect ratio of the image */
}

.product-card h3 {
  margin: 0.5rem 0; /* Margin around the title */
  color: #000; /* Dark color for the title for readability */
}

.product-card p {
  font-size: 0.9rem; /* Slightly smaller font size for the description */
  color: #000; /* Dark gray color for the paragraph */
  margin: 0.5rem; /* Margin around the paragraph */
}

.product-card button {
  background-color: #fd7e14; /* Bright color for the button */
  color: white; /* White text for the button */
  border: none; /* No border for the button */
  padding: 0.5rem 1rem; /* Padding inside the button */
  margin-bottom: 1rem; /* Margin at the bottom of the button */
  cursor: pointer; /* Pointer cursor on hover */
  border-radius: 4px; /* Rounded corners for the button */
}

.product-card button:hover {
  background-color: #e67300; /* Darker shade on hover */
}
