.faq-container {
    width: 80%;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .faq-container h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .faq {
    margin-bottom: 15px;
  }
  
  .faq h2 {
    color: #e67300;
    margin-bottom: 5px;
  }
  
  .faq p {
    color: #fff;
    line-height: 1.5;
  }
  