@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@700&display=swap');

header {
    display: flex;
    align-items: center;
    justify-content: space-between; /* Keeps items spaced out */
    background: linear-gradient(to right, #3C4142, #586856);
    padding: 1rem;
}

nav {
    display: flex;
    align-items: center;
}

header img {
    height: 77px; /* Consider using max-height for responsiveness */
}

nav a {
    color: #fff;
    font-family: 'Merriweather', serif;
    text-decoration: none;
    padding: 0.5em 1em;
}

.cart {
    margin-left: auto; /* Pushes cart to the right */
    display: flex;
    align-items: center;
}

.cart img {
    height: 44px; /* Consider using max-height for responsiveness */
}

nav a:hover {
    color: #9ea4a5;
}

.products {
    /* color: #fd7e14; */
}

.products:hover {
    /* color: #e67300; */
}

/* Responsive styles */
@media (max-width: 768px) {
    header {
        flex-direction: column;
        align-items: flex-start;
    }

    nav {
        margin-top: 1em;
        width: 100%;
        justify-content: space-between;
        flex-wrap: wrap; /* Allows items to wrap if space is constrained */
    }

    .cart {
        margin-left: 0;
        position: static; /* Ensure it's not absolutely positioned */
    }

    .cart img {
        height: 36px; /* Smaller size for mobile */
    }
}
